	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"We will begin with an overview of the Frames library that offers a data frame abstraction for efficiently working with tabular data in Haskell. A close look at an incidental aspect of the Frames implementation will provide an opportunity to think a bit about records and sum types, which will let us work up a technique for designing embedded domain specific languages (EDSLs) that run at top speed whether interpreted as ordinary Haskell values, or used to feed an entirely non-Haskell compiler tool chain for targeting specialized hardware.","meta":{"duration":"PT1H41M41S"},"presenter":{"name":"Anthony Cowley"},"source":{"id":"_KioQRICpmo","provider":"youtube"},"slug":"framing-the-discussion-with-edsls","tag":["Haskell"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/_KioQRICpmo/default.jpg","height":90,"width":120}},"title":"Framing the Discussion with EDSLs"}}]}},"pathContext":{"speaker":"Anthony Cowley"}}

